* {
    padding: 0;
    margin: 0;
}

#page {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #8dd1ec;
}

#page .logo-container p {
    margin-top: 10px;
    font-size: 20px;
    line-height: 34px;
    color: rgb(59, 63, 66);
    font-weight: bolder;
}

#page .infos {
    align-items: center;
}

#page .infos h2 {
    font-size: 66px;
    font-weight: 700;
    line-height: 70px;
    color: rgb(59, 63, 66);
    font-weight: bolder;
}